.DiaryEditor{
  border: 1px solid grey;
  text-align: center;
  padding: 20px;
}

.DiaryEditor input,textarea{
  margin-bottom: 20px;
  width: 500px;
  padding: 10px;
}

.DiaryEditor textarea{
  height: 150px;
}

.DiaryEditor select{
  width: 300px;
  padding: 10px;
  margin-bottom: 20px;
}

.DiaryEditor button{
  width: 500px;
  padding: 10px;
  cursor: pointer;
}

.DiaryEditor #alarm{
  width: 500px;
}

.DiaryList{
  border: 1px solid black;
  padding: 20px;
  margin-top : 20px;
}

.DiaryList h2{
  text-align: center;
}

.DiaryItem{
  background-color: skyblue;
  margin-bottom: 10px;
  padding: 20px
}

.DiaryItem .info{
  border-bottom:  1px solid black;
  padding-bottom: 10px;
  margin-bottom: 10px;
}

.DiaryItem .date{
  color:gray;
}

.DiaryItem .content{
  font-weight:  bold;
  margin-bottom: 30px;
  margin-top: 30px;
}